body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f9f3f3;
  background-image: url("https://www.transparenttextures.com/patterns/cubes.png");
  /* This is mostly intended for prototyping; please download the pattern and re-host for production environments. Thank you! */
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.main-container{
  max-width: 1600px;
  min-width: 380px;
  margin:auto;
}

.container {
  padding: 10px 30px;
  background-color: #a66cff;
  color: white;
}

.container h1 {
  margin: 0;
}

.title-description-container form {
  position: relative;
  width: 500px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  margin-top: 2rem;
  border: 1px solid #a66cff;
  background-color: white;
  border-radius: 0.3rem;
  padding: 5px;
  margin-bottom: 70px;
}

@media screen and (max-width: 500px) {
  .title-description-container form {
    width: 360px;
  }
  
}

.title-description-container .title {
  width: 100%;
  padding: 0.8rem;
  font-size: 1.4rem;
  border: none;
  margin-bottom: 10px;
  transition:5.5s ease-in-out;
}

.title-description-container .title:focus {
  outline: none;
}

.btn {
  position: absolute;
  bottom: -11%;
  right: 5%;
  width: 2.4rem;
  height: 2.4rem;
  font-size: 1.5rem;
  border-radius: 50%;
  border: 1px solid #a66cff;
  background-color: #a66cff;
  color: white;
  cursor: pointer;
}

.btn:hover {
  background-color: #31087b;
}

.title-addBtn-wrapper {
  display: flex;
  justify-content: space-between;
}

.title-description-container .description {
  padding: 10px;
  font-size: 1.2rem;
  border-top: none;
  resize: none;
  border: none;
}

.title-description-container .description:focus {
  outline: none;
}

.notes-container {
  width: 90%;
  margin: 1rem auto;
  display: flex;
  align-items: center;
  column-gap: 2rem;
  row-gap: 2rem;
  flex-wrap: wrap;
}

.box {
  position: relative;
  width: 400px;
  padding: 5px 15px 20px 15px;
  border: 1px solid #a66cff;
  border-radius: 0.3rem;
  background-color: #f9f9f9;
  overflow: hidden;
}

@media screen and (max-width: 400px) {
  .box{
    width: 360px;
  } 
}

.box h3 {
  margin: 0;
  text-align: center;
  font-size: 1.5rem;
  color: #303841;
}

.box p {
  padding: 0 10px;
}

.delete-btn {
  position: absolute;
  font-size: 1.2rem;
  bottom: 3%;
  right: 3%;
  cursor: pointer;
  color: #a66cff;
  font-weight: 500;
}

.delete-btn:hover {
  color: #31087b;
}
